
.work-window {
  margin: 15px 5px 5px 5px;
}
.category {
  margin: 15px 5px 5px 5px;
  height: 100%;
  width: 10%;
  position: fixed;
  z-index: 1;
  left: 0px;
}
.content {
  margin-left: 12%;
}
body {
  font: normal 14px "Trebuchet MS", Helvetica, sans-serif;
  margin: 0;
}
.fullscreen {
  width: 95%;
  height: 100%;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.split {
  width: 50%;
  height: 100%;
  position: fixed;
  z-index: 1;
  overflow-x: hidden;
  margin-top : 15px;
}
.right {
  width: 36%;
  right: 0px;
  padding-right: 10px;
}
.selected {
 background-color: aliceblue;
}
.green-button {
  float:right;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid rgba(27, 31, 35, .15);
  border-radius: 6px;
  -webkit-box-shadow: rgba(27, 31, 35, .1) 0 1px 0;
          box-shadow: rgba(27, 31, 35, .1) 0 1px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: grey;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  line-height: 5px;
  padding: 6px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
}
.green-button:focus:not(:focus-visible):not(.focus-visible) {
  color: white;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.green-button:hover {
  color: white;
  background-color: #2c974b;
}
.green-button:focus {
  -webkit-box-shadow: rgba(46, 164, 79, .4) 0 0 0 3px;
          box-shadow: rgba(46, 164, 79, .4) 0 0 0 3px;
  outline: none;
}
.green-button:disabled {
  background-color: #94d3a2;
  border-color: rgba(27, 31, 35, .1);
  color: rgba(255, 255, 255, .8);
  cursor: default;
}
.green-button:active {
  background-color: #298e46;
  -webkit-box-shadow: rgba(20, 70, 32, .2) 0 1px 0 inset;
          box-shadow: rgba(20, 70, 32, .2) 0 1px 0 inset;
}
button {
  background-color: rgba(51, 51, 51, 0.05);
  border-radius: 2px;
  border-width: 0;
  color: #333333;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  line-height: 5px;
  list-style: none;
  margin: 0;
  padding: 7px 7px;
  text-align: center;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  vertical-align: baseline;
  white-space: nowrap;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}


.selected {
 background-color: aliceblue;
}
.done {
 color: grey;
}
.done ul {
 text-decoration: line-through
}
.empty {
  color : white;
  background-color : #f0f0f5;
}
.input {
  padding: 5px 0px 5px 5px;
  font-size: medium;
}
.title {
  width: 99%;
  margin: 0px 5px 5px 0px;
  border-radius: 5px;
  border-width: 0px;
}
.desc {
  width: 99%;
  margin: 0px 5px 5px 0px;
  border-radius:5px;
  border-width:1px;
}
.listTask .task-row {
  padding : 2px 2px;
  border-bottom: 1px solid;
  border-width: 1px;
}
.task-desc {
  font-size: 13;
  padding-left: 10px;
  color: grey;
}
.task-title {
  font-size: 15;
}


.element {
  padding: 10px 0px 10px 10px;
  border-radius: 5px;
  width : 100%;
}
.category-editor {
  padding: 10px 0px 30px 10px;
  border-radius: 5px;
  width : 100%;
}
.category-remove-button {
  float:right;
  margin:5px 5px 5px 10px;
}
.category-add-button {
  float:right;
  margin: 5px 0px 0px 0px;
}
.edited {
  padding: 5px 5px 5px 5px;
  border-radius: 5px;
  width : 80%;
}


.i-category {
  margin: 5px 0px 0px 5px;
}

